import React from 'react'

const OpenSourceProjects = ( ) => {
    const opsProjects = [
        {
            img:'static/img/ops/graphiql-storm.png',
            title:'graphiql-storm',
            link:'https://github.com/Gherciu/graphiql-storm',
            description:'A GraphQl web IDE'
        },
        {
            img:'static/img/ops/graphiql-storm.png',
            title:'no-backend',
            link:'https://github.com/Gherciu/no-backend',
            description:'A firebase like service but self-hosted'
        }  
    ]
    return (
    <div className='content-wrapper'>
        <div className="ops-container">
        {opsProjects.map((item,index)=>
            <div className="ops-card-container" key={index}>
                <div className="ops-card" >
                    <div className="description">
                        <a href={item.link} target='__blank' className='ops-card-label'>
                            <img src='static/img/ops/github.png' />
                        </a>
                        <a href={item.link} target='__blank' className='ops-link'>
                            <h3 className='text-center'>
                                {item.title}
                            </h3>
                        </a>
                        <span className='text-center'>
                            {item.description}
                        </span>
                    </div>
                    <img src={item.img} className='ops-card-img' alt=""/>
                </div>
            </div>
        )}
        </div>
        <style jsx>{`
            .ops-container{
                display: flex;
                align-items: center;
                justify-content: space-around;
                flex-wrap: wrap;
                margin-bottom: 150px;
            }
            .ops-card-container{
                display: flex;
                align-items: center;
                flex-direction: column;
                max-width:400px;
            }
            .ops-card{
                width:450px;
                min-height:100px;
                border-radius:24px;
                overflow: hidden;
                background: #fff;
                margin-bottom: 20px;
                box-sizing: border-box;
                display: flex;
                align-items: center;
                justify-content: space-between;
                box-shadow: 0 10px 25px rgba(0,0,0,0.12), 9px 10px 45px rgba(0,0,0,0.24);
                position: relative;
            }
            .ops-card-label{
                display:block;
                position: absolute;
                top:10px;
                left:20px;
                width:40px;
                height:20px;
            }
            .description{
                padding:0px 15px;
                text-align: center;
                display: flex;
                flex-direction: column;
                align-items: center;
            }
            .ops-card-img{
                object-fit: cover;
                width:220px;
                height: 220px;
                box-sizing: border-box;
            }
            .ops-link{
                color: #333;
                text-decoration: none;
                font-size:18px;
                border-bottom: 3px solid #20AADB;
                display: block;
                padding: 0px 5px;
                line-height: 0px;
                width: auto;
                height:35px;
                margin-bottom: 10px;
                box-sizing: border-box;
                transition: all 0.5s;
            }
            .ops-link:hover{
                border-bottom: 30px solid #20AADB;
                color:#222;
            }
            .text-center{
                text-align: center;
            }
        `}</style>
    </div>
    )
}

export default OpenSourceProjects